@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-image: -webkit-gradient(linear,  left top, left bottom,  from(#222222),  to(#444444));
  background-image: linear-gradient(#222222,  #444444);
  color: white;
  text-shadow: 1px 3px 3px #22222250 !important;
  font-family: 'Roboto Mono', monospace;
}

.main {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader {
  color: #ff5346;
}

.data {
  font-size: 1.25rem;
  text-align: center;
}

.data svg {
  width: 200px;
  height: 200px;
  padding: 25px;
  fill: #ff5346;
  -webkit-animation: spin 60s infinite linear;
}

.data .time {
  margin: 15px;
  font-size: 1rem;
}

.data span {
  color: #ff5346;
  font-weight: bold;
}

.data .credits {
  color: #999999;
  margin-top: 25px;
  font-size: 0.75rem;
}

.data .credits a {
  color: #999999;
}

.data .credits a:hover {
  color: white;
}
@-webkit-keyframes spin {
  from { -webkit-transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); }
}
@keyframes spin {
  from {-webkit-transform:rotate(0deg);transform:rotate(0deg);}
  to {-webkit-transform:rotate(360deg);transform:rotate(360deg);}
}

